.all-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  display: flex;
  font-family: var(--font-inter);
  border-bottom: 1px solid var(--grey-border);
  width: 50%;
  padding: 2em 0;
}

.travel-cover-image {
  border-radius: 15px;
  height: 268px;
  width: 225px;
}


.card-text-div {
  padding: 2em;
}

/* Location Info */
.card-location-info {
  display: flex;
  align-items: center;
}

.location-country {
  color: var(--main-text-color);
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0.17em;
  margin: 0 2em 0 .5em;
}

.linkMaps {
  color: var(--link-maps-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 12px;
}


/* Location Descriptions */
.card-title {
  color:  var(--main-text-color);
  font-size: 40px;
  font-weight: 700;
  line-height: 30px;
  margin: 1rem 0 7% 0;
}

.card-date {
  color:  var(--main-text-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 12px;
  margin-bottom: 1em;
}

.card-description {
  color: var(--main-text-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
}


/* Media Query */
@media (max-width: 1100px){
  .card {
    width: 80%;
  }
}